<template>
  <div class="admin">
    <van-nav-bar title="消息"></van-nav-bar>
    <h1>{{ count }}</h1>
    <h1>{{ ChangeShowCom }}</h1>
    <!-- <h2>{{ChangeShowCom}}</h2> -->
    <h2>getters加工后的值:{{ getCount }}</h2>

    <button @click="addCount(num)">+</button>
    <button @click="delCount(num)">-</button>
    <button @click="cs">-</button>
    <br />
    <!-- <h3>$store.state. -->

    <router-link active-class="action" to="/index/message/left"
      >left</router-link
    >
    <br />
    <router-link active-class="action" to="/index/message/right"
      >right</router-link
    >
    <router-view></router-view>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations, mapActions } from "vuex";
export default {
  data() {
    return {
      active: 0,
      num: 5,
    };
  },
  methods: {
    cs() {
      console.log(this.$route.path);
    },
    ...mapMutations("Students", ["addCount", "delCount"]),
    //传递的方法是commit
    //结果为 0[object MouseEvent] NaN
    // @click="del"写法中del传递的是默认参数event，传递后于count相加
    ...mapActions("Students", ["getAddCount", "getDelCount"]),
  },
  computed: {
    // ...mapState({count:'count',ChangeShowCom:'ChangeShowCom'}),//对象写法
    ...mapState("Students", ["count", "ChangeShowCom"]), //简写（数组写法）

    // ...mapState({
    //   count1:state=>state.count
    // }),//函数写法
    ...mapGetters("Students", ["getCount"]),
  },
  // 路由器独有的生命周期函数
  activated() {
    console.log("11");
  },
  deactivated() {
    console.log("222");
  },
};
</script>

<style lang="css" scoped>
.router-link {
  margin-left: 50px;
}
.action {
  color: brown;
}
</style>
>
