import Vue from "vue";
import VueRouter from "vue-router";
import Index from "@/components/index";
import Home from "@/views/home";
import Find from "@/views/find";
import Message from "@/views/message";
import Details from "@/views/details";
import Setup from "@/views/setup";

import Goods from "@/views/personal/Goods";
import Left from "@/pages/left";
import Right from "@/pages/right";
import store from "@/store/store";

import Spxq from "@/views/Spxq";
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    component: Index,
    redirect: "index/home",
  },
  {
    path: "/index",
    component: Index,
    name: "index",
    redirect: "index/home",
    children: [
      {
        path: "/index/home",
        name: "home",
        component: Home,
      },
      {
        path: "/index/find",
        name: "find",
        component: Find,
        //路由元 meat
        meta: { isAuth: true },
        // 独享守卫
        // beforeEnter:((to,form,next) => {
        //   if(to.meta.isAuth){
        //     alert("你有权限")
        //     next()
        //   }else{
        //     alert("你没有权限")
        //   }
        // })
      },

      {
        path: "/index/message",
        name: "message",
        component: Message,

        children: [
          {
            path: "/index/message/left",
            name: "message/left",
            component: Left,
          },
          {
            path: "/index/message/right",
            component: Right,
          },
        ],
      },
      {
        path: "/index/setup",
        name: "setup",
        component: Setup,
      },
    ],
  },
  {
    path: "/details",
    name: "details",
    component: Details,
  },
  {
    path: "/goods/:id",
    name: "goods",
    component: Goods,
    props: true,
  },
  {
    path: "/data",
    name: "data",
    component: () => import("@/views/personal/Data"),
  },
  {
    path: "/spxq/:id",
    name: "spxq",
    component: Spxq,
  },
  {
    path: "/dt",
    name: "dt",
    component: () => import("@/components/Dt"),
  },
];

const router = new VueRouter({
  // 路由器默认hash模式，带有#，兼容性优于history模式
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

//路由切换之前
router.beforeEach((to, form, next) => {
  // store.commit('Loading/setLoading',true)
  // setTimeout(() => {
  // if(localStorage.getItem('name') == 'wzy') {
  // if(to.meta.isAuth){
  // alert("你没有权限")
  // }else{
  next();
  // }

  // }

  // },400)
}),
  //路由切换之后
  router.afterEach((to, form, next) => {
    // store.commit('Loading/setLoading',false)
  });

export default router;
